import { mapActions, mapState, mapMutations } from 'vuex';
export default {
	data() {
		return {
			url_config: ''
		};
	},
	computed: {
		...mapState(['handleOrder'])
	},
	filters: {
		joinAttr(arr) {
			let condition = arr.reduce((x, y, i) => {
				if(y.condition){
					if (i < arr.length - 1) {
						return x + y.condition + '/';
					} else {
						return x + y.condition;
					}
				}else{
					return x
				}
			}, '');
			return condition || '默认'
		}
	},
	onLoad() {
		this.url_config = getApp().globalData.url_config;
		console.log(this.handleOrder);
	}
};
